@import 'vars';
@import 'mixins';

.#{$prefix}pagination {
  display: flex;
  height: 30px;
  border-radius: 2px;
  border: 1px solid #ddd;
  box-sizing: border-box;

  &__column {
    display: flex;
    align-items: center;
    flex: 1;
    box-sizing: border-box;
    min-width: 70px;
    padding: 0 4px;
    justify-content: center;

    &--prev {
      justify-content: space-between;
      border-right: 1px solid #ddd; }

    &--next {
      justify-content: space-between;
      border-left: 1px solid #ddd; } }

  &__prev, &__first, &__next, &__last {
    height: 24px; } }
